<template>
<v-app id="FeedbackResults">
  
  <v-content id="FeedbackResultsId">
      <v-container fluid>
        <v-row
          align="center"
          justify="center"
        >   
            <v-card class="elevation-12" width="1000" >
              <v-img
                height="300"
                src="@/assets/banner_main.png"
              ></v-img>
              <br/>
              <div class="display-1" style="text-align:center; color:#00B293" @click="getfeedbackresults(eventid, loggedinuserid)">
                <b >{{title}}</b>
                <v-icon style="color:#00B293" @click="getfeedbackresults(eventid, loggedinuserid)">mdi-refresh</v-icon>
              </div>
              <v-divider class="mx-4"></v-divider>
              <br/>
                <v-list>
                <v-list-group
                  v-for="(item, index) in surveyQuestions"
                  :key="item.questionid"
                  @click="getfeedbackresultsdetails(loggedinuserid, eventid, index)"
                >
                  <template v-slot:activator>
                    <v-list-item-content>
                      <v-progress-linear
                        buffer-value=100
                        background-opacity=0.1
                        height=2
                        :value = item.averageRating
                        color="#00B293"
                      ></v-progress-linear>
                      <div class="question"> {{item.question}}   </div>     
                      <div class="ratings" v-if= "item.isratingenabled"> Ratings: <span class="rating-list" v-for="itm in item.ratingpercentage"  :key="itm.id"> {{itm.id}} - {{ (( itm.value / item.totalResponse) * 100).toFixed(2) }}% </span> </div> 
                      <div class="options"  v-if= "item.isradioenabled"> Options: <span class="rating-list" v-for="itm in item.optionsPercentage"  :key="itm.id"> {{isNaN(itm.id) ? itm.id : item.radiooptions[parseInt(itm.id)]}} - {{ (( itm.value / item.totalResponse) * 100).toFixed(2) }}% </span> </div>   
                    </v-list-item-content>
                  </template>
                    <v-list v-for="(comment, index) in item.responseDetails" :key="index" class="detailsList">
                      <div v-if= "item.isratingenabled"><b>Rating :</b> {{comment.rating}}</div>
                      <div v-if= "item.isradioenabled"><b>Selection :</b> {{item.radiooptions[parseInt(comment.selection)]}} </div>
                      <div><b>Comments :</b>{{comment.text}}</div>
                    </v-list>
                  <v-divider></v-divider>
                </v-list-group>
                
                </v-list>
            
            </v-card>


        </v-row>
      </v-container>
    </v-content>
</v-app>
</template>

<script>
import axios from "../axios";
export default {
  data(){
    return{
      loggedinuserid : null,
      eventid: null,
      title: "KALARAVA 2019 Feeback Results",

      surveyQuestions:[]
    }
  },
  methods: {
    isValidEmail: function(email){
        const emailRegexp = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/;
        return emailRegexp.test(email);
      },
    getfeedbackresults(event, access){
            let self = this;
            self.isprocessing=true;
            axios.get(`/feedbackresultsummary/${event}/${access}`)
            .then(function(res){
                self.surveyQuestions = res.data
              })
            .catch( function(error){
                alert(error)
            })
    },
    getfeedbackresultsdetails(access, event, index){
            let self = this;
            self.isprocessing=true;
            if(self.surveyQuestions[index].responseDetails.length === 0)
              axios.get(`/feedbackresultsdetsils/${access}/${event}/${self.surveyQuestions[index].questionid}`)
              .then(function(res){
                  self.surveyQuestions[index].responseDetails = res.data
                })
              .catch( function(error){
                  alert(error)
              })
    }
  },
  mounted()
  {
    this.loggedinuserid = this.$route.params.userid;
    this.eventid = this.$route.params.eventid;
    this.getfeedbackresults(this.eventid, this.loggedinuserid);
  }

}
</script>


<style scoped>
@import 'https://fonts.googleapis.com/css?family=Roboto:500|IBM+Plex+Sans:500|Heebo:500';

.question{
  font-size: 1.2em;
  font-family: "Roboto","IBM Plex Sans",sans-serif;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  font-weight: 600;
}

.ratings,.options{
  font-size: 1em;
  font-family: "IBM Plex Sans","Roboto",sans-serif;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  margin-left: 1em;
}

.rating-list{
  display: inline-flex;
  margin-left: 0.8em;
  margin-top: 0.5em;
  padding-left: 1.2em;
  padding-right: 1.2em;
  padding-top: 0.4em;
  padding-bottom: 0.4em;
  border: 1px solid black;
  border-radius: 5px; 
}

.detailsList{
  margin-left: 2.5em;
  margin-right: 3em;
  border-bottom: 1px solid gray;
  border-radius: 0px;
  padding-left: 0.5em;
  font-size: 0.9em;
  font-family: "IBM Plex Sans","Roboto",sans-serif;
}

</style>