<template>
<v-container>
<center><v-btn @click="loaddata">Refresh</v-btn></center>
<div class="d-flex flex-row flex-wrap ">

  <v-card class="mx-auto mt-2" max-width="340" md="2" outlined :loading="isloading" v-for="(item, index) in metrics" :key="index">
    <v-card-text>
    <v-row>
      <v-col>
        <div class="display-3 text--primary">
           <b> {{item.totalamount}}</b>
        </div>
        <v-label>{{item.from}}</v-label>
      </v-col>
      <v-col><div class="display-1 text--primary">{{item.totalregistration}}</div><v-label>Total Registration</v-label></v-col>
      </v-row>
    <v-row>
      
      <v-col><br/><div style="font-size:1.8em"> {{item.adults}} </div> <v-label>Adults</v-label> </v-col>
      <v-col><br/><div style="font-size:1.8em"> {{item.students}} </div> <v-label>Student</v-label> </v-col>
      <v-col><br/><div style="font-size:1.8em"> {{item.kidspaid}} </div> <v-label>Kids(10+)</v-label> </v-col>
      <v-col><br/><div style="font-size:1.8em"> {{item.kids}} </div> <v-label>Kids</v-label> </v-col>
    </v-row>
    </v-card-text>
  </v-card>
</div>
</v-container>
</template>

<script>
// @ is an alias to /src
import axios from "../axios";

export default {
  name: 'Statistics',
  components: {
  },
    props: {
      loggedName: String,
      loggedId: String,
      isAdmin: Boolean
    },
  data(){
    return{
        isloading: true,
        metrics: [{
            "totalamount" : 0,
            "from" : "Total Amount",
            "totalregistration":0,
            "adults" : 0,
            "students" : 0,
            "kids": 0,
            "kidspaid" :0
        }]
    }
  },
  methods: {
    loaddata()
    {
      let self = this;
      self.metrics = [{
            "totalamount" : 0,
            "from" : "Total Amount",
            "totalregistration":0,
            "adults" : 0,
            "students" : 0,
            "kids": 0,
            "kidspaid" :0
        }];
      self.isloading= true;
      axios.get("/statistics/RAJYOTHSAVA_2019/"+self.loggedId)
      .then(function(response){
            self.metrics = response.data;
            self.isloading= false
        })
      .catch( function(error){
          self.isloading= false;
          alert(error)
      })
    }
  },
  mounted() {
      this.loaddata()
  },
}
</script>