<template>
<v-app id="RegisterLogin">
  
  <v-content id="RegisterLogin">
      <v-container fluid>
        <v-row
          align="center"
          justify="center"
        >   
            <v-card class="elevation-12" width="1000" >
              <v-img
                height="300"
                src="@/assets/banner_main.png"
              ></v-img>
              <br/>
              <div class="display-1" style="text-align:center; color:#00B293"><b>Registration for Rajyotsava event occuring on 7th Dec 2019</b></div>
              <br/>
              <v-divider class="mx-4"></v-divider>
              <br/>

            <div class="ml-10 mr-10">
                <div class="pl-4 pr-4 pt-2 pb-2" style="border:1px solid grey; border-radius:5px; font-size:0.95em">
                    <p>Kannada Rajyotsava is a flagship event organized and celebrated by Kannada community around the world, 
                    and Sirigannada Koota is organizing this event for past 8 years in Munich. This year Sirigannada Koota is hosting its 9th year Rajyotsava as <b>KALARAVA</b>.</p>
                    <p>KALARAVA is a platform for social and cultural activities, and is been held on <b>7th December 2019</b> (14:00-20:00) at 
                    <i><a href="https://goo.gl/maps/RrfPPCDjiSP4GoYg6">Kulturzentrum Trudering, Wasserburger Landstrae 32, 81825 Mnchen</a></i>.</p>
                    <p>
                    There is a small entry fee to accommodate the event expenses, and the details are as below.
                    <ul style="margin-left:1em; margin-top:0.5em; margin-bottom:0.6em">
                      <li> Adult : <b>16 </b> <strike>(Early Bird : <b>14</b>)</strike></li>
                      <li> Student (18+) : <b>12</b></li>
                      <li> Kid/Student (11 - 18years) : <b>10</b></li>
                      <li> Kid (upto 11 years and accompanied by parents) : free </li>
                      <li> <b>Early Bird Offer ends on 24th Nov 2019</b> </li>
                    </ul></p>
                    All Paid Tickets <b><i>includes snacks</i></b> and a lottery/Raffle is arranged for the ticket number checked in before 2PM, 
                    and the lucky one will be awarded with attractive price.
                    <br/><b>**NOTE**</b>: <u>On spot registration are subjected to availability of Tickets. </u>
                </div>
                <br/>
              <v-text-field label="Full Name" outlined v-model="name"></v-text-field>
              <v-text-field  label="Email" outlined v-model="email"></v-text-field>
              <v-row class="ml-2 mr-2">
                <v-select :items="noofpersons" label="Number of Adults"  outlined v-model="numberofadults"></v-select>
                <v-select :items="noofpersons" label="Number of Students(18+)"  outlined v-model="numberofStudents"></v-select>
              </v-row>
              <v-row class="ml-2 mr-2">
                <v-select :items="noofpersons" label="Number of Kids(age>10 & age<18)"  outlined v-model="numberofPaidKids"></v-select>
                <v-select :items="noofpersons" label="Number of Kids(age <=10)"  outlined v-model="numofkids"></v-select>
              </v-row>
              
              <div style="background-color: teal; color: white; border: 1px solid teal; border-radius:5px; padding-left: 8px; padding-top: 5px; padding-bottom: 3px; margin-left: 20px; margin-bottom: 25px; width:200px;">Total Amount : <b>{{pricetobepaid}}</b> Euros</div>
              
              
              <v-checkbox
                class="ml-5"
                v-model="isUnderstood"
                label= "I understand that the ticket for the event is generated only after confirmation of the payment"
              ></v-checkbox>
              <v-checkbox
                class="ml-5"
                label= "I would like to subscribe for Sirigannada Koota notifications on future events"
                v-model="isSubscribed"
              ></v-checkbox>

              <v-card  class="mx-auto"   outlined  >
                <v-list-item three-line>
                  <v-list-item-content>
                    <v-list-item-title class="headline mb-1">Payment Details:</v-list-item-title>
                    <v-card-subtitle>
                      You can transfer the amount to the below bank details by specifying the registration number(generated after submission) or email id 
                      in the remarks/comments section. We will generate the ticket as soon as we receive the payment. Bank details are as follow.
                      <table style="border-collapse: collapse; border: none; margin-top: 12px; margin-bottom:12px; margin-left: 10px; font-size:1em">
                        <tr style="border: none;">
                          <td style="border: none;padding-right:3px">Name</td>
                          <td style="border: none;">: <b>Somanagouda Patil</b></td>
                        </tr>
                        <tr style="border: none;">
                          <td style="border: none;">IBAN</td>
                          <td style="border: none;">: <b>DE45 7004 0041 0660 9861 00</b></td>
                        </tr>
                        <tr style="border: none;">
                          <td style="border: none;">Bank</td>
                          <td style="border: none;">: <b>Commerzbank, Munich</b></td>
                        </tr>
                      </table>
                      Or you can contact us at <a href="mailto:info@sirigannadakoota.org">info@sirigannadakoota.org</a> to get in touch with one of the volunteers.
                    </v-card-subtitle>
                    <v-card-subtitle>
                      <b>NOTE</b> : This registration doesn't ensure the reservation of seat, untill ticket is generated. All tickets are mailed to the email id provided above.
                    </v-card-subtitle>
                  </v-list-item-content>
                </v-list-item>
              </v-card>
            </div>
            <p class="ml-12 mr-8" style="font-size:0.75em; color:gray">**If you don't receive an email of this registration, please write back to us on info@sirigannadakoota.org</p>
            <center>
              <v-btn class="mt-2" color="success" x-large dark @click="selfregister" v-show="!isprocessing"> Register </v-btn>
              <v-btn class="mt-2" color="success" x-large dark v-show="isprocessing"> <v-progress-circular  indeterminate  color="white"></v-progress-circular> </v-btn>
            </center>           

            <v-card-subtitle><center> For more information on the events, do follow us on 
                              <b><a href="https://www.facebook.com/groups/sirigannadakutamunich" target="_blank">facebook</a></b>
                              or you can also reach us on <b><a href="mailto:info@sirigannadakoota.org">info@sirigannadakoota.org</a></b></center></v-card-subtitle>
             
            </v-card>
        </v-row>
      </v-container>
    </v-content>
    <v-dialog v-model="dialog" max-width="290" >
      <v-card> <v-card-title class="headline">{{isError ? 'Error' : 'Success'}}</v-card-title>  <v-card-text>
          {{errorText}}
        </v-card-text>
        <v-card-actions><v-spacer></v-spacer><v-btn color="green darken-1" text @click="dialog = false" > Close  </v-btn> </v-card-actions>
      </v-card>
    </v-dialog>
</v-app>
</template>

<script>
import axios from "../axios";
export default {
  data(){
    return{
      isprocessing: false,
      noofpersons : [0,1,2,3,4],
      isSubscribed : false,
      isUnderstood : false,
      name: "",
      email: "",
      numberofadults: 1,
      numberofStudents:0,
      numofkids: 0,
      numberofPaidKids: 0,
      dialog : false,
      isError: true,
      errorText : null,
      aamountperperson: 16,
      ramountperperson: 14,
      samountperperson: 12,
      kamountperperson: 10,
      earlyBirdEndDate : '11/25/2019'
    }
  },
  computed:{
    pricetobepaid(){
      return new Date() <= new Date(this.earlyBirdEndDate) ? ((this.ramountperperson * this.numberofadults)+(this.samountperperson * this.numberofStudents)+(this.kamountperperson * this.numberofPaidKids)) : ((this.aamountperperson * this.numberofadults)+(this.samountperperson * this.numberofStudents)+(this.kamountperperson * this.numberofPaidKids));
    }
  },
  methods: {
    isValidEmail: function(email){
        const emailRegexp = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/;
        return emailRegexp.test(email);
      },
    selfregister(){
        
        if(this.name.trim() === null || this.name.trim() === undefined || this.name.trim() === "")
        {
            this.errorText = "We expect a name for registration";
            this.isError=true;
            this.dialog = true;
        }
        else if(this.email.trim() === null || this.email.trim() === undefined || this.email.trim() === "")
        {
            this.errorText = "We expect a email for registration";
            this.isError=true;
            this.dialog = true
        }
        else if(!this.isValidEmail(this.email))
        {
            this.errorText = "We expect an correct email id for Registration";
            this.isError=true;
            this.dialog = true
        }
        else if(this.numberofadults === 0 && this.numberofStudents === 0)
        {
            this.errorText = "Please select number of Adults/Students attending the event.";
            this.isError=true;
            this.dialog = true
        }
        else if(!this.isUnderstood)
        {
            this.errorText = "Registration cannot continue without understanding that tickets cannot be generated without confirming payment.";
            this.isError=true;
            this.dialog = true
        }
        else
        {
            let self = this;
            self.isprocessing=true;
            axios.post("/selfregistration/",
                    {
                      "eventid":"RAJYOTHSAVA_2019",
                      "name" : self.name,
                      "email" : self.email,
                      "numberOfAdults": self.numberofadults,
                      "numberOfKids": self.numofkids,
                      "numberOfPaidKids": self.numberofPaidKids,
                      "numberOfStudents": self.numberofStudents,
                      "amountCalculated": self.pricetobepaid,
                      "subscribe" : self.isSubscribed ? 1 : 0
                    })
            .then(function(response){

                    self.isError=false;
                    self.dialog = true;
                    self.errorText = "Registration ID : "+response.data + ". Registered Successfully, Please check you inbox for further instructions on payment.";
                    self.name = "";
                    self.email = "";
                    self.numberofadults = 1;
                    self.numofkids = 0;
                    self.numberofStudents = 0;
                    self.numberofPaidKids = 0;
                    self.isSubscribed = false;
                    self.isUnderstood = false;
                    self.isprocessing=false;
                
              })
            .catch( function(error){
                self.isError=true;
                self.dialog = true;
                self.errorText = error;
                self.isprocessing=false;
            })
        }
    }
  }
}
</script>


<style scoped>

.eventtext{
  font-size: 1.3em;
  margin-top: 8px;
}

</style>