<template>
  <div>
    
    <v-expansion-panels
      v-model="panel"
      light
      focusable
    >
      <v-expansion-panel >
        <v-expansion-panel-header><center><b> Search Options</b></center></v-expansion-panel-header>
        <v-expansion-panel-content>
            <br/>

          <v-select :items="eventlist" label="Select Event"  outlined v-model="eventid"></v-select>

          <v-text-field
           label="Registration ID"
           outlined
           v-model="searchonregid"
          ></v-text-field>

          <v-text-field
           label="Email"
           outlined
           v-model="searchonemail"
          ></v-text-field>
    
         <center>
         <v-btn
          color="green"
          dark
          @click="search(eventid, searchonemail.trim(), searchonregid.trim())"
        >Search</v-btn></center>
   
        </v-expansion-panel-content>
      </v-expansion-panel>

        <v-progress-circular
              indeterminate
              color="red"
              v-show="inProgress"
        ></v-progress-circular>
        <div v-show="inProgress"> <b>Loading...</b></div>

      <div v-show="!inProgress"> <b>{{results.length ? 'Search Results' : 'No Results Available'}}</b></div>
      <v-expansion-panel v-for="(item,index) in results" :key="index" v-show="results.length">
        <v-expansion-panel-header> {{item.name}}</v-expansion-panel-header>
        <v-expansion-panel-content>
          <table>
              <tr>
                  <td width="150px">Reg ID</td>
                  <td>: {{item.id}}</td>
              </tr>
              <tr>
                  <td width="150px">Event ID</td>
                  <td>: {{item.eventid}}</td>
              </tr>
              <tr>
                  <td width="150px">Email</td>
                  <td>: {{item.email}}</td>
              </tr>
              <tr>
                  <td>Number of Adults</td>
                  <td>: {{item.noOfAdults}}</td>
              </tr>
              <tr>
                  <td>Number Of Students(18+)</td>
                  <td>: {{item.noOfStudents}}</td>
              </tr>
              <tr>
                  <td>Number of Kids(10+)</td>
                  <td>: {{item.noOfKidsPaid}}</td>
              </tr>
              <tr>
                  <td>Number of Kids(free)</td>
                  <td>: {{item.noOfKids}}</td>
              </tr>
              <tr>
                  <td>Calculated Amt</td>
                  <td>: {{item.amount}}</td>
              </tr>
              <tr>
                  <td>Registered on</td>
                  <td>: {{item.createdOn}}</td>
              </tr>
          </table><br/>
          
                <v-row v-show="item.displaytoast">
                  <v-spacer></v-spacer>
                <v-chip
                  close
                  :color="item.isError ? 'red':'green'"
                  text-color="white"
                  @click:close="item.displaytoast = false"
                >
                  {{item.responseText}}
                </v-chip>
                 <v-spacer></v-spacer>
                </v-row>

                <v-row v-show="item.displaytoast && !item.isError">
                    <v-spacer></v-spacer>
                    <img :src="item.qrCode" :alt="item.accessKey">
                    <v-spacer></v-spacer>
                </v-row>

                <v-row v-show="item.displaytoast && !item.isError">
                    <v-spacer></v-spacer>
                    <label> This Code has been mailed</label>
                    <v-spacer></v-spacer>
                </v-row>

          <center>
            <v-btn color="primary" v-show="!isprocessing" @click="issueTicket(index, item.name, item.email, item.noOfAdults, item.noOfStudents, item.noOfKidsPaid, item.noOfKids, item.amount )" :disabled="item.isticketissued">Confirm Payment & Issue Ticket</v-btn>
            <v-btn class="mt-2" color="success" x-large dark v-show="isprocessing"> <v-progress-circular  indeterminate  color="white"></v-progress-circular> </v-btn>
          </center>

        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>

<script>
import axios from "../axios";

  export default {
    props: {
      loggedName: String,
      loggedId: String,
      isAdmin: Boolean
    },
    data: () => {
        return {
            searchonemail:"",
            searchonregid:"",
            eventid:"",
            eventlist:['JAGALIKATTE_2019','RAJYOTHSAVA_2019'],
            inProgress: false,
            panel : 0,
            results:[]
        }
    },
    methods: {
      issueTicket(index, name, email, adults, students, paidkids, kids, amt)
      {
        let self = this;
        //alert(name + " : "+email+" : "+adults+" : "+students+" : "+paidkids+ " : "+kids+" : "+amt);
        axios.post("/registration/"+this.loggedId,
                    {
                      "eventid":"RAJYOTHSAVA_2019",
                      "name" : name,
                      "email" : email,
                      "numberOfAdults": adults,
                      "numberOfPaidkids": paidkids,
                      "numberOfKids": kids,
                      "numberOfStudents": students,
                      "amountPaid" : parseFloat(amt.replace(/[^0-9\.]+/g,""))
                    })
        .then(function(response){
            if(response.data.accessCode)
              self.results[index].qrCode = response.data.accessCode
            else
              self.results[index].accessKey = response.data.accessKey
              self.results[index].isError = false;
              self.results[index].responseText = response.data.status;
              self.results[index].displaytoast = true;
              self.results[index].isprocessing = false;
              self.results[index].isticketissued = true
          })
        .catch( function(error){
            self.results[index].isError = true;
            self.results[index].displaytoast = true;
            self.results[index].responseText = error;
            self.results[index].isprocessing = false;
        })
      },
        search(eventid, email, regid){
            let self = this;
            if( (eventid === null || eventid === undefined || eventid === "") && (email === null || email === undefined || email === "") && (regid === null || regid === undefined || regid === ""))
            {
                alert("Enter Search Details")
            }
            else
            {
                self.inProgress = true;
                self.results = [];
                axios.post("/searchselfregister/"+self.loggedId,
                {
                  "email" : email,
                  "registrationid" : regid,
                  "eventid":eventid
                })
                .then(function(response){
                      self.panel = -1;
                      self.inProgress = false;
                      self.results = response.data.map(x => Object.assign({isError: false, displaytoast: false, responseText:"", isprocessing: false}, x));
                  })
                .catch( function(error){
                    alert(error)
                })

            }
            
        }
    },
  }
  </script>

  <style scoped>
  table {
    border-collapse: collapse;
    width: 100%;
    font-size: 0.9em;
    }
 
    th, td {
        padding: 8px;
        text-align: left;
        border-bottom: 1px solid #ddd;
    }

  </style>