<template>
<v-app id="RegisterLogin">
  
  <v-content id="RegisterLogin">
      <v-container fluid>
        <v-row
          align="center"
          justify="center"
        >   
            <v-card class="elevation-12" width="1000" >
              <v-img
                height="300"
                src="@/assets/banner_main.png"
              ></v-img>
              <br/>
              <div class="display-1" style="text-align:center; color:#00B293"><b>Registration for Pratibha Kaaranji occuring on 17th Nov 2019</b></div>
              <br/>
              <v-divider class="mx-4"></v-divider>
              <br/>

            <div class="ml-10 mr-10">
              <v-text-field label="Full Name" outlined v-model="name"></v-text-field>
              <v-text-field  label="Email" outlined v-model="email"></v-text-field>
              <v-row class="ml-2 mr-2">
                <v-select :items="noofpersons" label="Number of Adults"  outlined v-model="numofadults"></v-select>
                <v-select :items="noofpersons" label="Number of Kids"  outlined v-model="numofkids"></v-select>
              </v-row>
              <v-checkbox
                class="ml-5"
                v-model="isSubscribed"
                label= "I would like to subscribe for Sirigannada Koota notifications on future events"
              ></v-checkbox>
            </div>
            <center>
              <v-btn class="mt-2" color="success" x-large dark @click="selfregister" v-show="!isprocessing"> Register </v-btn>
              <v-btn class="mt-2" color="success" x-large dark v-show="isprocessing"> <v-progress-circular  indeterminate  color="white"></v-progress-circular> </v-btn>
            </center>
            <v-card-subtitle><center> For more information on the events, do follow us on 
                              <b><a href="https://www.facebook.com/groups/sirigannadakutamunich" target="_blank">facebook</a></b>
                              or you can also reach us on <b><a href="mailto:info@sirigannadakoota.org">info@sirigannadakoota.org</a></b></center></v-card-subtitle>
             
            </v-card>


        </v-row>
      </v-container>
    </v-content>
    <v-dialog v-model="dialog" max-width="290" >
      <v-card> <v-card-title class="headline">{{isError ? 'Error' : 'Success'}}</v-card-title>  <v-card-text>
          {{errorText}}
        </v-card-text>
        <v-card-actions><v-spacer></v-spacer><v-btn color="green darken-1" text @click="dialog = false" > Close  </v-btn> </v-card-actions>
      </v-card>
    </v-dialog>
</v-app>
</template>

<script>
import axios from "../axios";
export default {
  data(){
    return{
      isprocessing: false,
      noofpersons : [0,1,2,3,4],
      isSubscribed : false,
      isUnderstood : false,
      name: "",
      email: "",
      numofadults: 1,
      numofstudents:0,
      numofkids: 0,
      dialog : false,
      isError: true,
      errorText : null
      
    }
  },
  methods: {
    isValidEmail: function(email){
        const emailRegexp = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/;
        return emailRegexp.test(email);
      },
    selfregister(){
        if(this.name.trim() === null || this.name.trim() === undefined || this.name.trim() === "")
        {
            this.errorText = "We expect a name for registration";
            this.isError=true;
            this.dialog = true;
        }
        else if(this.email.trim() === null || this.email.trim() === undefined || this.email.trim() === "")
        {
            this.errorText = "We expect a email for registration";
            this.isError=true;
            this.dialog = true
        }
        else if(!this.isValidEmail(this.email))
        {
            this.errorText = "We expect an correct email id for Registration";
            this.isError=true;
            this.dialog = true
        }
        else if(this.numofadults === 0 )
        {
            this.errorText = "Please select number of Adults/Students attending the event.";
            this.isError=true;
            this.dialog = true
        }
        else
        {
            let self = this;
            self.isprocessing=true;
            axios.post("/selfregistration/",
                    {
                      "eventid":"PRATIBHA_KAARANJI_2019",
                      "name" : self.name,
                      "email" : self.email,
                      "numberOfAdults": self.numofadults,
                      "numberOfKids": self.numofkids,
                      "numberOfStudents": self.numofstudents,
                      "numberOfPaidKids": 0,
                      "subscribe" : self.isSubscribed ? 1 : 0,
                      "amountCalculated" : 0
                    })
            .then(function(response){

                    self.isError=false;
                    self.dialog = true;
                    self.errorText = "Registered Successfully, Registration ID : "+response.data;
                    self.name = "";
                    self.email = "";
                    self.numofadults = 1;
                    self.numofkids = 0;
                    self.isSubscribed = false;
                    self.isprocessing=false;
                
              })
            .catch( function(error){
                self.isError=true;
                self.dialog = true;
                self.errorText = error;
                self.isprocessing=false;
            })
        }
    }
  }
}
</script>


<style scoped>

.eventtext{
  font-size: 1.3em;
  margin-top: 8px;
}

</style>