import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import login from './views/Login.vue'
import registration from './views/Registration.vue'
import event1 from './views/Event1.vue'
import event2 from './views/Event2.vue'
import subscribe from './views/Subscribe.vue'
import feedback from './views/Feedback.vue'
import feedbackresults from './views/FeedbackResults.vue'

Vue.use(Router)

export default new Router({
  mode: 'abstract',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/login',
      name: 'Login',
      component: login
    },
    {
      path: '/subscribe',
      name: 'Subscribe',
      component: subscribe
    },
    {
      path: '/event1',
      name: 'Jagallikatte',
      component: event1
    },
    {
      path: '/event2',
      name: 'Rajyothasava',
      component: event2
    },
    {
      path: '/feedback',
      name: 'Feebback',
      component: feedback
    },
    {
      path: '/feedbackresults/:userid/:eventid',
      name: 'FeedbackResults',
      component: feedbackresults
    },
    {
      path: '/registration/:id/:isadmin/:usercreation/:userregistration/:userverification/:name',
      name: 'Registration',
      component: registration
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    }
  ]
})
