<template>
  <div id="app">
    <div id="nav">
      <ul class="desktop">
        <li><router-link to="/login">Login</router-link></li>
        <!-- <li><router-link to="/event2" v-show="showreg">Rajyotsava Registration</router-link> </li> -->
        <!-- <li><router-link to="/event1">Pratibha Kaaranji Registration</router-link> </li> -->
        <!-- <li><router-link to="/feedback">Feedback</router-link></li> -->
        <li><router-link to="/subscribe">Subscribe</router-link></li>
        <li><router-link to="/about">About</router-link></li>
        <li><router-link to="/">Home</router-link> </li>
      </ul> 
      <ul class="mobile">
        <v-btn class="mx-2" fab dark color="teal" @click="expand = !expand">
          <v-icon dark>mdi-format-list-bulleted-square</v-icon>
        </v-btn>
        <span v-show="expand">
        <v-row >
          <li><router-link to="/">Home</router-link> </li>
          <li><router-link to="/about">About</router-link></li>
          <!-- <li><router-link to="/feedback">Feedback</router-link></li> -->
          <li><router-link to="/subscribe">Subscribe</router-link></li>
          <li><router-link to="/login">Login</router-link></li>
          <!-- <li><router-link to="/event1">Pratibha Kaaranji Registration</router-link> </li> -->
          <!-- <li><router-link to="/event2">Rajyotsava Registration</router-link> </li> -->
        </v-row>
        </span>
      </ul>
    </div>
    <router-view/>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'Login',
  mounted() {
    (new URL(document.location)).searchParams.toString().includes('subscribe') ? 
      this.$router.push(`/subscribe?${(new URL(document.location)).searchParams}` ) : 
      (new URL(document.location)).searchParams.toString().includes('feedback') ? 
      this.$router.push("/feedback") : this.$router.push("/");
  },
  data(){
    return{
      expand: false,
      closeRegistration: new Date("12/04/2019 23:59"),
      showreg : true
    }
  },
  created() {
    this.showreg = this.closeRegistration > new Date();
  },
  methods: {
    navigate(to){
      if( to === 'home')
        this.$router.push("/")
      else if( to === 'about')
        this.$router.push("/about")
      else if( to === 'event1')
        this.$router.push("/event1")
      else if( to === 'event2')
        this.$router.push("/event2")
      else if( to === 'login')
        this.$router.push("/login")
    }
  }
}
</script>

