<template>
<v-app id="RegisterLogin">
  
  <v-content id="RegisterLogin">
      <v-container fluid>
        <v-row
          align="center"
          justify="center"
        >   
            <v-card class="elevation-12" width="1000" >
              <v-img
                height="300"
                src="@/assets/banner_main.png"
              ></v-img>
              <br/>
              <div class="display-2" style="text-align:center; color:#F2600C"><b> Privacy Policy </b></div>
              <br/>
              <v-divider class="mx-4"></v-divider>
              <br/>

            <div class="ml-10 mr-10">
              <div class="privacytext" style="text-align:left;">
              Sirigannadakoota Munich (SKM), is a Kannada community, with the motive of providing the platform to encourage cultural and social activities  
              among people living in and around Munich. With respect to this we organize several events around the year.
              <p>We need your Name, Email and your consent to keep you informed about the events organized by SMK, 
              Please tick the below check box and subscribe to the mailing list.</p>
            </div>
              
              <v-text-field label="Name" outlined v-model="name"></v-text-field>
              <v-text-field  label="Email" outlined v-model="email"></v-text-field>
            </div>

            <v-checkbox
                class="ml-10"
                v-model="consent"
                label= "I consent SKM to store my details to send me information about their events, meetups and  campaigns."
              ></v-checkbox>

              <div class="privacyfooter" style="text-align:left;">
                SKM do not spam your inbox, and do not share your details with any third party. 
                All the information are stored securely, and you can withdraw your consent to use your information, or ask us to amend or delete, 
                by emailing to <a href="mailto:support@sirigannadakoota.org">support@sirigannadakoota.org</a>
              </div>
            <br/>
            <div class="d-flex justify-center flex-wrap justify-md-space-around">
         
              <v-btn class="mt-2" color="success" x-large dark @click="subscribe(1)"> Subscribe </v-btn>
          
            </div>

            <br/>

            <v-card-subtitle><center> For more information on the events, do follow us on 
                              <b><a href="https://www.facebook.com/groups/sirigannadakutamunich" target="_blank">facebook</a></b>
                              or you can also reach us on <b><a href="mailto:info@sirigannadakoota.org">info@sirigannadakoota.org</a></b></center></v-card-subtitle>
             
            </v-card>


        </v-row>
      </v-container>
      <v-dialog v-model="dialog" max-width="290" >
        <v-card> <v-card-title class="headline"></v-card-title>  <v-card-text>
            <div style="font-size:1.1em"> {{dialogText}} </div>
          </v-card-text>
          <v-card-actions><v-spacer></v-spacer><v-btn color="green darken-1" text @click="dialog = false" > Close  </v-btn> </v-card-actions>
        </v-card>
      </v-dialog>
    </v-content>
</v-app>
</template>

<script>
import axios from "../axios";
export default {
  data(){
    return{
      name : null,
      email : null,
      consent : false,
      dialog: false,
      dialogText: ""
    }
  },
  methods: {
    isEmpty(data)
    {
      if(data === null || data === undefined || data === "" || !data)
        return true
      else
        return false
    },
    isValidEmail: function(email){
        const emailRegexp = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/;
        return emailRegexp.test(email);
      },
    subscribe()
    {
        if(this.isEmpty(this.name))
        {
          this.dialog = true;
          this.dialogText = "We expect your name to subscribe."
        }
        else if(this.isEmpty(this.email))
        {
          this.dialog = true;
          this.dialogText = "We expect your email to subscribe."
        }
        else if(!this.isValidEmail(this.email))
        {
          this.dialog = true;
          this.dialogText = "We expect an valid email to subscribe."
        }
        else if(this.isEmpty(this.consent))
        {
          this.dialog = true;
          this.dialogText = "We expect your consent to subscribe."
        }
        else
        {
          let self = this;
            axios.post("/subscribe/",
                    {
                      "name" : self.name,
                      "email" : self.email
                    })
            .then(function(){
                self.dialog = true;
                self.dialogText = 'Thank you. You have subscribed successfully.' 
              })
            .catch( function(error){
                self.dialog = true;
                self.dialogText = error;
            })
          
        }
    }
  },
  created() {
    this.name = this.$route.query.name;
    this.email = this.$route.query.email;
  },
}
</script>


<style scoped>

.privacytext{
  font-size: 1em;
 
  margin-top: 8px;
  line-height: 3em;
}
.privacyfooter{
  font-size: 0.9em;
  padding-left: 3em;
  padding-right: 3em;
  margin-top: 8px;
  line-height: 2em;
}

</style>