<template>
  <div>
    
    <v-expansion-panels
      v-model="panel"
      light
      focusable
    >
      <v-expansion-panel >
        <v-expansion-panel-header><center><b> Search Options</b></center></v-expansion-panel-header>
        <v-expansion-panel-content>
            <br/>
          <v-text-field
           label="Email"
           outlined
           v-model="searchonemail"
         ></v-text-field>
         <v-text-field
           label="Created By"
           outlined
           v-model="searchoncreatedby"
         ></v-text-field>
         <center>
         <v-btn
          color="green"
          dark
          @click="search(searchonemail.trim(), searchoncreatedby.trim())"
        >Search</v-btn></center>
   
        </v-expansion-panel-content>
      </v-expansion-panel>

        <v-progress-circular
              indeterminate
              color="red"
              v-show="inProgress"
        ></v-progress-circular>
        <div v-show="inProgress"> <b>Loading...</b></div>

      <div v-show="!inProgress"> <b>{{results.length ? 'Search Results' : 'No Results Available'}}</b></div>
      <v-expansion-panel v-for="(item,index) in results" :key="index" v-show="results.length">
        <v-expansion-panel-header> {{item.name}}</v-expansion-panel-header>
        <v-expansion-panel-content>
          <table>
              <tr>
                  <td width="150px">Email</td>
                  <td>: {{item.email}}</td>
              </tr>
              <tr>
                  <td>Number of Adults</td>
                  <td>: {{item.noOfAdults}}</td>
              </tr>
              <tr>
                  <td>Number Of Students(18+)</td>
                  <td>: {{item.noOfStudents}}</td>
              </tr>
              <tr>
                  <td>Number of Kids(10+)</td>
                  <td>: {{item.noOfKidsPaid}}</td>
              </tr>
              <tr>
                  <td>Number of Kids(free)</td>
                  <td>: {{item.noOfKids}}</td>
              </tr>
              <tr>
                  <td>Registered on</td>
                  <td>: {{item.createdOn}}</td>
              </tr>
              <tr>
                  <td>Registered By</td>
                  <td>: {{item.createdBy}}</td>
              </tr>
          </table>
          <v-textarea
          auto-grow
          outlined
          prefix="Comments : "
          v-model="item.comments"
          >
          </v-textarea>
          <center><v-btn color="#FF6319" @click="deregister(item.registeredaccessKey, item.email, item.comments, index)" :disabled="item.deregistered === 1"> DEREGISTER</v-btn></center>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>

<script>
import axios from "../axios";

  export default {
    props: {
      loggedName: String,
      loggedId: String,
      isAdmin: Boolean
    },
    data: () => {
        return {
            searchonemail:"",
            searchoncreatedby:"",
            inProgress: false,
            panel : 0,
            results:[]
        }
    },
    methods: {
        deregister(registeredaccessKey, email, comments, index){
            let self = this;
            if(comments === null || comments === undefined)
                alert("Comments are mandatory")
            else
                axios.post("/deregister/"+self.loggedId,
                {
                  "email" : email,
                  "accessid" : registeredaccessKey,
                  "comments" : comments,
                  "eventid":"RAJYOTHSAVA_2019"
                })
                .then(function(response){
                      if(response.data === "1" || response.data === 1)
                      {
                          self.results[index].deregistered = 1
                      }
                      else
                        alert("Cannot Deregistered")
                  })
                .catch( function(error){
                    alert(error)
                })

        },
        search(email, createdby){
            let self = this;
            if( (email === null || email === undefined || email === "") && (createdby === null || createdby === undefined || createdby === ""))
            {
                alert("Enter Search Details")
            }
            else
            {
                self.inProgress = true;
                self.results = [];
                axios.post("/search/"+self.loggedId,
                {
                  "email" : email,
                  "createdBy" : createdby,
                  "eventid":"RAJYOTHSAVA_2019"
                })
                .then(function(response){
                      self.panel = -1;
                      self.inProgress = false;
                      self.results = response.data;
                  })
                .catch( function(error){
                    alert(error)
                })

            }
            
        }
    },
  }
  </script>

  <style scoped>
  table {
    border-collapse: collapse;
    width: 100%;
    font-size: 0.9em;
    }
 
    th, td {
        padding: 8px;
        text-align: left;
        border-bottom: 1px solid #ddd;
    }

  </style>