<template>
<v-app id="FeedBack">
  
  <v-content id="FeedBackid">
      <v-container fluid>
        <v-row
          align="center"
          justify="center"
        >   
            <v-card class="elevation-12" width="1000" >
              <v-img
                height="300"
                src="@/assets/banner_main.png"
              ></v-img>
              <br/>
              <div class="display-1" style="text-align:center; color:#00B293"><b>{{title}}</b></div>
              <br/>
              <v-divider class="mx-4"></v-divider>
              <br/>

            <div class="ml-10 mr-10 description">
              {{description}}
            </div>
            <div class="ml-10 mr-10 mt-3 note"> ** All the comments are anonymised and will have no space to identify the responder </div>
            <div class="ml-10 mr-10 mt-10" v-for="item in survey" :key="item.id">
              <span class="question">{{item.question}}</span>
              <span class="rating" v-if="item.rating"> <v-rating v-model="item.ratingValue"></v-rating></span>
              <span class="rating" v-if="item.radio"><v-radio-group v-model="item.radioValue" row><v-radio v-for="n in item.radioOptions" :key="n" :label="n"></v-radio></v-radio-group></span>
              <span class="response"> <v-textarea class="mx-2"  rows="1" auto-grow v-model="item.response" placeholder="Comments" ></v-textarea></span>
              <!-- <span class="response"> <v-textarea filled rows="1" auto-grow v-model="item.response"></v-textarea></span> -->
            </div>            
            
            <center>
              <v-btn class="mt-2" color="success" x-large dark @click="savefeedback" v-show="!isprocessing"> Submit </v-btn>
              <v-btn class="mt-2" color="success" x-large dark v-show="isprocessing"> <v-progress-circular  indeterminate  color="white"></v-progress-circular> </v-btn>
            </center> 
            <br/>     
            <div class="footertext">
              {{footerText}}
            </div>   
            <br/>     
            </v-card>


        </v-row>
      </v-container>
    </v-content>
    <v-dialog v-model="dialog" max-width="290" >
      <v-card> <v-card-title class="headline">{{isError ? 'Error' : 'Success'}}</v-card-title>  <v-card-text>
          {{dialogText}}
        </v-card-text>
        <v-card-actions><v-spacer></v-spacer><v-btn color="green darken-1" text @click="dialog = false, isprocessing = false" > Close  </v-btn> </v-card-actions>
      </v-card>
    </v-dialog>
</v-app>
</template>

<script>
import axios from "../axios";
export default {
  data(){
    return{
      title: "KALARAVA 2019 Feeback Form",
      description: "#KALARAVA, Thank you for participating in this grand event, without your support it wouldn't have been successfull. Hope you enjoyed the event, and we expect your support for our next events too. Now its time for us to do some retrospective, and we need your help. Kindly spare some time to answer the below questions, which will help us to improve our next events.",
      description_old:"#KALARAVA, Yeah, Its a grand success now as intended. We all worked hard and extended our help for each other with the single motive to make this event successfull. Thanks you for each and every one who took out there time to achieve this success. We all did our best for this year. We might still have some space for improvements for next events in all the departments. We request you to kindly spare some time in answering below questions. We can learn from your comments/responses.",
      footerText:"Thank you, See you in our next event..!",
      survey:[
      {
        id: 1,
        question: "How did you got to know about KALARAVA program ?",
        response:"",
        rating: false,
        ratingValue : 0,
        radio: true,
        radioOptions: ['Facebook','Email','Website','Meetup','Friends','Posters','Others (Please specify)'],
        radioValue:""
      },
      {
        id: 2,
        question: "How was the marketing of the program (information and communication) ?",
        response:"",
        rating: true,
        ratingValue : 0,
        radio: false
      },
      {
        id: 3,
        question: "How do you rate ticketing/registration system ?",
        response:"",
        rating: true,
        ratingValue : 0,
        radio: false
      },
      {
        id: 4,
        question: `How do you rate the concept of "One Kannada Family" ?`,
        response:"",
        rating: true,
        ratingValue: 0,
        radio: false
      },
      {
        id: 5,
        question: `How do you rate the cultural performances ?`,
        response:"",
        rating: true,
        ratingValue: 0,
        radio: false
      },
      {
        id: 6,
        question: `How do you rate the support from our Volunteers ?`,
        response:"",
        rating: true,
        ratingValue: 0,
        radio: false
      },
      {
        id: 7,
        question: `How do you rate the entry fee for the program ?`,
        response:"",
        rating: true,
        ratingValue: 0,
        radio: false
      },
      {
        id: 8,
        question: `Would you suggest us to include food for the event ? If yes, what would be the fair range of price that you like to spare for such event ?`,
        response:"",
        rating: false,
        ratingValue : 0,
        radio: true,
        radioOptions: ['Yes','No', 'Maybe'],
        radioValue:""
      },
      {
        id: 9,
        question: `How do you rate the overall programme ?`,
        response:"",
        rating: true,
        ratingValue: 0,
        radio: false
      },
      {
        id: 10,
        question: `Would you like to Volunteer for upcoming events of Sirigannada Koota ? If Yes, drop your name, email and Whatsapp number in the comments.`,
        response: "",
        rating: false,
        ratingValue : 0,
        radio: true,
        radioOptions: ['Yes','No', 'Maybe'],
        radioValue:""
      },
      {
        id: 11,
        question: `Do you have any other suggestion/comments about the Event or about the Community?`,
        response: "",
        rating: false,
        ratingValue : 0,
        radio: false
      }],
      dialog : false,
      isError: false,
      dialogText : "Thank you for your response",
      isprocessing: false
    }
  },
  methods: {
    isValidEmail: function(email){
        const emailRegexp = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/;
        return emailRegexp.test(email);
      },
    savefeedback(){
            let self = this;
            self.isprocessing=true;
            axios.post("/feedback/RAJYOTHSAVA_2019",self.survey.map(x => ({"id": x.id, "responseText": x.response, "ratingValue": x.ratingValue, "radioValue": x.radioValue }) ))
            .then(function(){
                self.dialog = true;
                self.dialogText = "Thank you for your valuable feedback. See you soon in next event";
                self.isError = false;
                self.survey.map(x => (x.ratingValue = 0, x.radioValue = "", x.response = "") );
                self.isprocessing=false;
              })
            .catch( function(error){
                self.dialog = true;
                self.dialogText = error+". Contact info@sirigannadakoota.org";
                self.isError = true;
                self.isprocessing=false;
            })
    }
  }
}
</script>


<style scoped>
@import 'https://fonts.googleapis.com/css?family=Roboto|IBM+Plex+Sans:500|Heebo:500';

.description{
  font-size: 1.2em;
  font-family: "Roboto","IBM Plex Sans",sans-serif;
}

.question{
  font-size: 1.1em;
  padding-left: 0.5em;
  font-family: "Heebo","IBM Plex Sans",sans-serif;
  font-weight: 500;
}

.note{
  font-size: 1em;
  font-family: "Roboto","IBM Plex Sans",sans-serif;
  color: #FF6840
}

.footertext{
  font-size: 1em;
  font-family: "Roboto","IBM Plex Sans",sans-serif;
  text-align: center;
  letter-spacing: 0.09em
}

</style>